$background: #282c34;
$background-transp: rgba(40, 44, 52, 0.8);
$offwhite: #cccccc;

.navbar {
  z-index: 10;
  position: sticky;
  width: 100%;
  top: 0;
  background-color: $background;
  color: white;
  height: 40px;
  box-shadow: 0px 8px 5px rgba(30, 34, 42, 0.6);
}

.nav-name {
  display: flex;
  flex-direction: row;
  margin: 0;
  width: 500px;
}

.nav-text {
  background-color: $background;
  z-index: 10;
}

.numbers {
  font-size: 22px;
  margin: 2px 0 0 0;
}

.close,
.m {
  position: absolute;
}

.close {
  left: 188px;
  padding-right: 100px;
}

.nav-container {
  align-items: flex-end;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 15px;
}

.navbar-home {
  color: $offwhite;
  text-decoration: none;
}

.nav-link {
  padding-right: 5px;
  transition: 1s;
  text-decoration: none;
  color: white;
  font-size: 18px;
}

.nav-link:hover {
  text-decoration: underline;
}

.nav-link-container {
  display: flex;
  flex-direction: row;
}

.under {
  transition: 2s;
  width: 190px;
  border-bottom: 2px solid white;
}
