
@media (max-width: 1100px) {
  .about-me {
    font-size: 1.7vw;
    width: 45%;
  }

  .about-container {
    padding: calc(50vh - (1.7vw * 7) + 40px) 0;
  }
}

@media (max-width: 1000px) {
  .about-container {
    flex-direction: column;
  }

  .about-me {
    text-align: center;
    width: 80vw;
  }

  .about-tag {
    font-size: 3vw;
  }

  .about-tag-container {
    width: 90%;
    justify-content: space-around;
    display: flex;
    flex-direction: row;
  }

  .project-pic {
    width: 450px;
  }

  .project-header {
    font-size: 52px;
  }

  .svg {
    width: 50px;
  }


  .national-parks2 {
    margin-top: 100px;
  }

  .project-desc-cont {
    width: 450px;
  }

  .links {
    display: none;
  }
}

@media (max-width: 800px) {
  .about-me {
    font-size: 14px;
  }

  .about-me {
    width: 95vw;
  }

  .about-container {
    height: 120px;
    padding: calc(50vh - 120px + 40px) 0;
  }

  .project-pic {
    // margin-top: 50px;
  }
  .project-pic-hover {
    display: none;
  }

  .project-container {
    height: 400px;
  }

  .project-desc-cont {
    width: 85vw;
  }

  .project-header {
    width: 450px;
  }

  .quackle2, .community2, {
    margin: 0;
    padding: 0;
  }

  .svg {
    width: 40px;
  }

  .quackle2 {
    padding-left: 100px;
    margin-top: 35px;
    margin-left: 150px;
    width: 200px;
    line-height: 80px;
  }

  .search-icon {
    margin-left: 175px;
  }

  .community2 {
    text-align: center;
    margin-top: 35px;
  }
}

@media (max-width: 550px) {
  .about-container {
    flex-direction: row;
  }

  .about-tag {
    font-size: 16px;
  }

  .about-tag-container {
    flex-wrap: wrap;
  }

  .footer-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .icon-wrapper {
    margin-top: 10px;
  }

  .back-to-top {
    display: none;
  }
}


