$background: #282c34;
$background-transp: rgba(40, 44, 52, 0.8);
$offwhite: #cccccc;

.about {
  border-bottom: 4px solid black;
  background-color: $offwhite;
}

.about-container {
  flex-wrap: wrap;
  align-items: center;
  color: $background;
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  border-radius: 5px;
  width: 99vw;
  margin: 0 auto;
  padding: calc(50vh - (3.7vw * 3) + 40px) 0;
}

.about-me {
  color: $background;
  width: 555px;
  text-align: right;
  font-size: 20px;
}

.about-tag {
  margin-bottom: 5px;
  font-weight: 900;
  font-size: 3.7vw;
  letter-spacing: 1px;
  white-space: nowrap;
}

.skills {
  width: 60px;
  height: 60px;
  margin: 15px;
}

.technologies {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  text-align: center;
}
