$background: #282c34;
$offwhite: #cccccc;

a {
  text-decoration: none;
}

.back-to-top {
  color: $background;
  font-weight: 900;
  margin-top: 3px;
  cursor: pointer;
}

.footer-container {
  border-top: 4px solid black;
}

.footer {
  position: relative;
  width: 100%;
  background-color: $offwhite;
  height: 70px;
  overflow: hidden;
  z-index: 4;
}

.icon-hover {
  transition: 1s;
  color: $background;
  font-weight: 900;
  width: 90px;
  text-align: center;
}

.icon-wrapper {
  margin-top: 15px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.info-wrapper {
  color: $background;
  margin-top: -10px;
  text-align: center;
}

