.job-description {
  width: 40vw;
}

.logo-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  width: 80vw;
  margin: 0 auto;
}

.tech-logo {
  width: 100px;
  opacity: 0.5;
  transition: 0.5s;
}

.tech-logo:hover {
  opacity: 1;
}

.express {
  width: 100px;
  transition: 0.5s;
  filter: brightness(1000%) contrast(0%);
}

.express:hover {
  filter: none;
}

.work-container {
  color: white;
  height: 75vh;
  background: rgba(49, 53, 62, 0.7);
}

.work-header {
  padding-top: 45px;
  text-align: center;
}

.work-history-container {
  padding: 50px 50px 10px 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  margin: 0 auto;
}

.work-logo {
  width: 500px;
}
