$background: #282c34;
$offwhite: #cccccc;

.portfolio {
  transition: 1s;
}

.portfolio:hover {
  background-color: rgba(49, 53, 62, 0.7);
}

.portfolio-header {
  width: 100vw;
  color: $offwhite;
  margin: 0 auto;
  text-align: center;
  padding: 45px 0;
}

.portfolio-title {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  color: $offwhite;
}
