@import url('https://fonts.googleapis.com/css?family=Righteous&display=swap');
@import url('https://fonts.googleapis.com/css?family=Grand+Hotel&display=swap');
@import url('https://fonts.googleapis.com/css?family=Oxygen+Mono|Wendy+One');
$background: #282c34;
$background-transp: rgba(40, 44, 52,0);
$black-fade: #292828;
$bloggle: #222222;
$black-fade-transp: rgba(41, 40, 40, .7);
$offwhite: #cccccc;

.community {
  border-radius: 15px;
}

.community2 {
  text-shadow: 2px 2px purple;
  margin-left: 115px;
  margin-top: 40px;
}

.community3 {
  margin-top: -100px;
}

.project-container {
  align-items: center;
  flex-wrap: wrap;
  background-color: $background;
  height: 500px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  transition: 1s;
}

.project-container:hover {
  background-color: rgba(49, 53, 62, 0.7);
}

.project-wrapper {
  align-items: center;
  max-width: 600px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: relative;
}


.quackle {
  border-radius: 25px;
  background-color: $offwhite;
  color: $background;
  font-size: 100px;
  max-width: 575px;
  padding-left: 25px;
}

.quackle2 {
  border-radius: 12px;
  padding-left: 90px;
  height: 85px;
  width: 295px;
  border: 3px solid $black-fade-transp;
  letter-spacing: 9px;
  margin-left: 175px;
  margin-top: 35px;
}

.quackle3 {
  margin-top: -100px;
}

.bloggle2 {
  padding: 15px 0;
  margin-top: -30px;
  width: 600px;
  text-align: center;
  background-color: $bloggle;
  border-radius: 15px;
}

.bloggle3 {
  margin-top: -175px;
}

.search-icon {
  color: $black-fade-transp;
  font-size: 50px;
  margin-bottom: -90px;
  margin-left: 195px;
}

.project-desc {
  text-align: center;
  color: $offwhite;
}

.project-desc-cont {
  overflow-wrap: break-word;
  // min-width: 500px;
  width: 40%;
}

.project-header {
  cursor: pointer;
  z-index: 2;
  font-family: Righteous;
  font-size: 70px;
  position: absolute;
  transform: translate(0%, 0%);
}

.project-pic-hover {
  border-radius: 5px;
  z-index: 3;
  transition: .5s;
  opacity: 0;
  position: absolute;
}

.project-pic-hover:hover {
  opacity: 1;
}


.toDo2 {
  background-color: #404040;
  border-radius: 15px;
  width: 590px;
  text-align: center;
  padding: 15px 0;
  text-shadow: -1px -1px 1px rgb(33, 33, 33);
  border: 5px solid #212529;
  margin-top: -40px;
  font-size: 65px;
}

.toDo3 {
  margin-top: -175px;
}

.national-parks {
  z-index: 1;
  filter: invert(100%);
  border-radius: 15px;
}

.national-parks2 {
  margin-top: 132px;
  margin-left: 8px;
}

.national-parks3 {
  margin-top: -50px;
}


.project-pic {
  width: 600px;
}

.svg {
  opacity: .5;
  padding: 15px;
  width: 75px;
  transition: .7s;
}

.svg:hover {
  opacity: .9;
}

.backed {
  opacity: 1;
  filter: brightness(1000%) contrast(0%);
}

.backed:hover {
  filter: none;
}

.tech-container {
  align-items: center;
  justify-content: space-evenly;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.links {
  margin: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.project-links {
  text-decoration: underline;
  color: $offwhite;
}

.project-links:active {
  text-decoration: line-through;
}
