@import url(https://fonts.googleapis.com/css?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css?family=Grand+Hotel&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oxygen+Mono|Wendy+One);
body, pre {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.about {
  border-bottom: 4px solid black;
  background-color: #cccccc; }

.about-container {
  flex-wrap: wrap;
  align-items: center;
  color: #282c34;
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  border-radius: 5px;
  width: 99vw;
  margin: 0 auto;
  padding: calc(50vh - (3.7vw * 3) + 40px) 0; }

.about-me {
  color: #282c34;
  width: 555px;
  text-align: right;
  font-size: 20px; }

.about-tag {
  margin-bottom: 5px;
  font-weight: 900;
  font-size: 3.7vw;
  letter-spacing: 1px;
  white-space: nowrap; }

.skills {
  width: 60px;
  height: 60px;
  margin: 15px; }

.technologies {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  text-align: center; }

* {
  margin: 0;
  padding: 0; }

body {
  background-color: #282c34; }

.app-body {
  background-color: #282c34; }

.App-logo {
  height: 40vmin; }

.App-link {
  color: #09d3ac; }

.navbar {
  z-index: 10;
  position: -webkit-sticky;
  position: sticky;
  width: 100%;
  top: 0;
  background-color: #282c34;
  color: white;
  height: 40px;
  box-shadow: 0px 8px 5px rgba(30, 34, 42, 0.6); }

.nav-name {
  display: flex;
  flex-direction: row;
  margin: 0;
  width: 500px; }

.nav-text {
  background-color: #282c34;
  z-index: 10; }

.numbers {
  font-size: 22px;
  margin: 2px 0 0 0; }

.close,
.m {
  position: absolute; }

.close {
  left: 188px;
  padding-right: 100px; }

.nav-container {
  align-items: flex-end;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 15px; }

.navbar-home {
  color: #cccccc;
  text-decoration: none; }

.nav-link {
  padding-right: 5px;
  -webkit-transition: 1s;
  transition: 1s;
  text-decoration: none;
  color: white;
  font-size: 18px; }

.nav-link:hover {
  text-decoration: underline; }

.nav-link-container {
  display: flex;
  flex-direction: row; }

.under {
  -webkit-transition: 2s;
  transition: 2s;
  width: 190px;
  border-bottom: 2px solid white; }

a {
  text-decoration: none; }

.back-to-top {
  color: #282c34;
  font-weight: 900;
  margin-top: 3px;
  cursor: pointer; }

.footer-container {
  border-top: 4px solid black; }

.footer {
  position: relative;
  width: 100%;
  background-color: #cccccc;
  height: 70px;
  overflow: hidden;
  z-index: 4; }

.icon-hover {
  -webkit-transition: 1s;
  transition: 1s;
  color: #282c34;
  font-weight: 900;
  width: 90px;
  text-align: center; }

.icon-wrapper {
  margin-top: 15px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center; }

.info-wrapper {
  color: #282c34;
  margin-top: -10px;
  text-align: center; }

.footer-icon {
  font-size: 40px;
  color: #282c34;
  background-color: #cccccc; }

.portfolio {
  -webkit-transition: 1s;
  transition: 1s; }

.portfolio:hover {
  background-color: rgba(49, 53, 62, 0.7); }

.portfolio-header {
  width: 100vw;
  color: #cccccc;
  margin: 0 auto;
  text-align: center;
  padding: 45px 0; }

.portfolio-title {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  color: #cccccc; }

.scroll-container {
  width: 100vw;
  z-index: 9;
  position: -webkit-sticky;
  position: sticky;
  top: 25px;
  height: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center; }

.font-aws {
  color: #282c34;
  position: absolute;
  cursor: pointer;
  font-stretch: expanded; }

.font-aws-bot {
  border: 5px solid #282c34;
  border-radius: 50%;
  padding: 5px 10px;
  -webkit-transition: color 1s ease;
  transition: color 1s ease;
  -webkit-transition: background-color 1s ease;
  transition: background-color 1s ease;
  -webkit-transition: border 1s ease;
  transition: border 1s ease;
  font-size: 55px;
  left: calc(50vw - 42.5px);
  top: 80vh; }

.scroll-text {
  font-weight: 900;
  position: absolute;
  top: 91vh;
  text-align: center; }

.community {
  border-radius: 15px; }

.community2 {
  text-shadow: 2px 2px purple;
  margin-left: 115px;
  margin-top: 40px; }

.community3 {
  margin-top: -100px; }

.project-container {
  align-items: center;
  flex-wrap: wrap;
  background-color: #282c34;
  height: 500px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  -webkit-transition: 1s;
  transition: 1s; }

.project-container:hover {
  background-color: rgba(49, 53, 62, 0.7); }

.project-wrapper {
  align-items: center;
  max-width: 600px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: relative; }

.quackle {
  border-radius: 25px;
  background-color: #cccccc;
  color: #282c34;
  font-size: 100px;
  max-width: 575px;
  padding-left: 25px; }

.quackle2 {
  border-radius: 12px;
  padding-left: 90px;
  height: 85px;
  width: 295px;
  border: 3px solid rgba(41, 40, 40, 0.7);
  letter-spacing: 9px;
  margin-left: 175px;
  margin-top: 35px; }

.quackle3 {
  margin-top: -100px; }

.bloggle2 {
  padding: 15px 0;
  margin-top: -30px;
  width: 600px;
  text-align: center;
  background-color: #222222;
  border-radius: 15px; }

.bloggle3 {
  margin-top: -175px; }

.search-icon {
  color: rgba(41, 40, 40, 0.7);
  font-size: 50px;
  margin-bottom: -90px;
  margin-left: 195px; }

.project-desc {
  text-align: center;
  color: #cccccc; }

.project-desc-cont {
  overflow-wrap: break-word;
  width: 40%; }

.project-header {
  cursor: pointer;
  z-index: 2;
  font-family: Righteous;
  font-size: 70px;
  position: absolute;
  -webkit-transform: translate(0%, 0%);
          transform: translate(0%, 0%); }

.project-pic-hover {
  border-radius: 5px;
  z-index: 3;
  -webkit-transition: .5s;
  transition: .5s;
  opacity: 0;
  position: absolute; }

.project-pic-hover:hover {
  opacity: 1; }

.toDo2 {
  background-color: #404040;
  border-radius: 15px;
  width: 590px;
  text-align: center;
  padding: 15px 0;
  text-shadow: -1px -1px 1px #212121;
  border: 5px solid #212529;
  margin-top: -40px;
  font-size: 65px; }

.toDo3 {
  margin-top: -175px; }

.national-parks {
  z-index: 1;
  -webkit-filter: invert(100%);
          filter: invert(100%);
  border-radius: 15px; }

.national-parks2 {
  margin-top: 132px;
  margin-left: 8px; }

.national-parks3 {
  margin-top: -50px; }

.project-pic {
  width: 600px; }

.svg {
  opacity: .5;
  padding: 15px;
  width: 75px;
  -webkit-transition: .7s;
  transition: .7s; }

.svg:hover {
  opacity: .9; }

.backed {
  opacity: 1;
  -webkit-filter: brightness(1000%) contrast(0%);
          filter: brightness(1000%) contrast(0%); }

.backed:hover {
  -webkit-filter: none;
          filter: none; }

.tech-container {
  align-items: center;
  justify-content: space-evenly;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; }

.links {
  margin: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly; }

.project-links {
  text-decoration: underline;
  color: #cccccc; }

.project-links:active {
  text-decoration: line-through; }

.job-description {
  width: 40vw; }

.logo-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  width: 80vw;
  margin: 0 auto; }

.tech-logo {
  width: 100px;
  opacity: 0.5;
  -webkit-transition: 0.5s;
  transition: 0.5s; }

.tech-logo:hover {
  opacity: 1; }

.express {
  width: 100px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  -webkit-filter: brightness(1000%) contrast(0%);
          filter: brightness(1000%) contrast(0%); }

.express:hover {
  -webkit-filter: none;
          filter: none; }

.work-container {
  color: white;
  height: 75vh;
  background: rgba(49, 53, 62, 0.7); }

.work-header {
  padding-top: 45px;
  text-align: center; }

.work-history-container {
  padding: 50px 50px 10px 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  margin: 0 auto; }

.work-logo {
  width: 500px; }

@media (max-width: 1100px) {
  .about-me {
    font-size: 1.7vw;
    width: 45%; }
  .about-container {
    padding: calc(50vh - (1.7vw * 7) + 40px) 0; } }

@media (max-width: 1000px) {
  .about-container {
    flex-direction: column; }
  .about-me {
    text-align: center;
    width: 80vw; }
  .about-tag {
    font-size: 3vw; }
  .about-tag-container {
    width: 90%;
    justify-content: space-around;
    display: flex;
    flex-direction: row; }
  .project-pic {
    width: 450px; }
  .project-header {
    font-size: 52px; }
  .svg {
    width: 50px; }
  .national-parks2 {
    margin-top: 100px; }
  .project-desc-cont {
    width: 450px; }
  .links {
    display: none; } }

@media (max-width: 800px) {
  .about-me {
    font-size: 14px; }
  .about-me {
    width: 95vw; }
  .about-container {
    height: 120px;
    padding: calc(50vh - 120px + 40px) 0; }
  .project-pic-hover {
    display: none; }
  .project-container {
    height: 400px; }
  .project-desc-cont {
    width: 85vw; }
  .project-header {
    width: 450px; }
  .quackle2, .community2 {
    margin: 0;
    padding: 0; }
  .svg {
    width: 40px; }
  .quackle2 {
    padding-left: 100px;
    margin-top: 35px;
    margin-left: 150px;
    width: 200px;
    line-height: 80px; }
  .search-icon {
    margin-left: 175px; }
  .community2 {
    text-align: center;
    margin-top: 35px; } }

@media (max-width: 550px) {
  .about-container {
    flex-direction: row; }
  .about-tag {
    font-size: 16px; }
  .about-tag-container {
    flex-wrap: wrap; }
  .footer-container {
    display: flex;
    flex-direction: column;
    justify-content: center; }
  .icon-wrapper {
    margin-top: 10px; }
  .back-to-top {
    display: none; } }

